/* *,
*::after,
*::before {
  box-sizing: border-box;
} */

body {
  font-family: 'Mina', sans-serif;
}

#root {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.video {
  width: 460px;
  margin: 10px;
  padding: 10px 10px 1px;
  background: #000000;
}

.video iframe {
  display: block;
  margin: 0 auto;
  width: 460px;
  height: 240px;
  border: none;
}

.video .date {
  margin: 6px 0 0 0;
  text-align: right;
  font-size: 80%;
  color: #cccccc;
}

.App {
  width: 1600px;
  /* min-height: 100vh; */
  margin-left: auto;
  margin-right: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  font-size: 1.2rem;
}


